import { products } from './productData.js';

const productCard = document.querySelector('.product-card');
const params = new URLSearchParams(window.location.search);
const productId = Number(params.get('id'));
const product = products.find(p => p.id === productId);

if (product) {
  productCard.insertAdjacentHTML('beforeend', `
    <div class="product-detail">
      <h2>${products.name}</h2>
      <p><strong>–û–ø–∏—Å:</strong> ${products.description}</p>
      <p><strong>–î–µ—Ç–∞–ª—ñ:</strong> ${products.details}</p>
      <p><strong>–¶—ñ–Ω–∞:</strong> ${products.price} –≥—Ä–Ω</p>
      <button class="product__add">–î–æ–¥–∞—Ç–∏ –≤ –∫–æ—Ä–∑–∏–Ω—É</button>
    </div>
  `);
} else {
  productCard.textContent = '–¢–æ–≤–∞—Ä –Ω–µ –∑–Ω–∞–π–¥–µ–Ω–æ.';
}